.NewsPage {
    margin: 0 0 0 auto;
    //overflow-y: auto;
    //overflow-x: hidden;
    //display: flex;
    width: 80%;

}

.ContainerContent {
    display: flex;
    width: 100%;
}

.None {
    display: none;
}

.CardNews {
    margin: 30px 20px 20px 20px;
    background: var(--app-accent);
    border-radius: 5px;
    padding: 5px;
}

.HeaderNews {
    display: block;
    font: var(--font-small-main);
    margin: 10px auto 0 auto;
    color: rgba(18, 18, 18, 0.7);
    width: 95%;
}

.PhotoNews {
    width: 100%;
    height: auto;
    border-radius: 5px;
}

.ContainerPhoto {
    width: 97%;
    height: 330px;
    border-radius: 5px;
    margin: 10px auto;
    overflow: hidden;
    position: relative;
}

.HeaderPhoto {
    font: var(--font-medium-main);
    color: var(--app-background);
    text-decoration: underline;
    position: absolute;
    bottom: 15px;
    left: 15px;
}

.Views {
    display: flex;
    position: absolute;
    right: 0;
}

.Calendar {
    margin: 10px 30px 0 10px;
}

.Button {
}

.BottomNews {
    display: flex;
    position: relative;
    margin: 20px auto;
    width: 95%;
}

.ContainerNews {
    width: calc(100% - 350px);
    margin: 0 40px;
}

@media screen and (max-width: 1140px) {
    .ContainerNews {
        width: 100%;
        margin: 0 20px;
    }

}

@media screen and (max-width: 1420px) {
    .NewsPage {
        width: 100%;
        max-width: 100%;
        margin-left: auto;
        margin-top: 100px;
        //overflow-y: auto;
        padding: 0 10px;
    }

    .CardNews {
        max-width: 950px;
        margin: 20px auto;
        background: var(--app-accent);
        border-radius: 5px;
    }
    .HeaderNews {
        font-size: 16px;
    }
    .ContainerPhoto {
        height: auto;
        max-height: 410px;
    }


}

@media screen and (max-width: 576px) {
    .ContainerPhoto {
        height: auto;
    }

    .BottomNews {
        display: flex;
        flex-wrap: wrap;
        flex-direction: column-reverse;
        position: relative;
        margin: 20px auto;
        width: 95%;
    }
    .Views {
        display: flex;
        position: static;
        margin-top: -15px;
        width: 100%;
    }
    .Button {
        flex-grow: 1;
    }
    .Calendar {
        margin: 10px auto 0 10px;
    }
    .HeaderPhoto {
        font-size: 18px;
    }


}

.navbar {
    height: 120px;
    background-color: aqua;
    position: sticky;
    top: -100px;
}

.wrapper {
    display: flex;
    background-color: bisque;
}

.content {
    background-color: blueviolet;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
}

.sidebar {
    background-color: aquamarine;
    width: 315px;
    height: 100%;
    min-height: 400px;
    top: 125px;
    position: sticky;
}

.card {
    margin: 10px;
    width: 600px;
    height: 500px;
    background-color: red;
}